.home-container{    
    position: relative;
    min-height: 100vh;
    padding: 50px 40px;
    margin-top: 7px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home-container .logo { 
    position: inherit;
    margin-top: -50px;
    height: 520px;
    width: 520px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    user-select: none;
}

.home-container .logo .main-img {   
    position: absolute;
    height: 300px;
    width: 300px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    cursor: pointer;
}

.home-container .logo .main-img.active { 
    animation: spin 8s linear 1s infinite; 
}

@keyframes spin {   
    to {    
        transform: rotate(360deg);
    }
}

.home-container .logo .circle { 
    position: absolute;
    height: 140px;
    width: 140px;
    border-radius: 50%;
    background-color: rgb(4, 4, 44);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.2s ease;
    opacity: 0;
}

.home-container .logo .main-img.active .circle {    
    opacity: 1;
}

.home-container .logo .main-img.active .circle:nth-child(1){    
    background: url(../props/html.png);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translateY(-210%);
}

.home-container .logo .main-img.active .circle:nth-child(2){    
    background: url(../props/css.png);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translate(150%, -150%);
}

.home-container .logo .main-img.active .circle:nth-child(3){    
    background: url(../props/js.png);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translate(210%);
}

.home-container .logo .main-img.active .circle:nth-child(4){    
    background: url(../props/bootstrap.png);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translate(150%, 150%);
}

.home-container .logo .main-img.active .circle:nth-child(5){    
    background: url(../props/react.png);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translateY(210%);
}

.home-container .logo .main-img.active .circle:nth-child(6){    
    background: url(../props/mysql.png);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translate(-150%, 150%);
}

.home-container .logo .main-img.active .circle:nth-child(7){    
    background: url(../props/csharp.png);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translate(-210%);
}

.home-container .logo .main-img.active .circle:nth-child(8){    
    background: url(../props/java.png);
    background-size: cover;
    background-position: center;
    position: absolute;
    transform: translate(-150%, -150%);
}

.home-container img {   
    position: absolute;
    height: 300px;
    width: 300px;
    border-radius: 100%;
    border: 3px solid rgb(108, 161, 197);
    transform: translate(-200px);
    opacity: 0;
    animation: fade-in 1s ease 1 0.5s forwards;
}

@keyframes fade-in {    
    to {    
        transform: translate(0);
        opacity: 1;
    }
}

.home-container h2{ 
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 60%;
    margin-top: 50px;
}

.home-container h2 span {   
    font-size: 28px;
    opacity: 0.8;
    padding-bottom: 5px;
    border-bottom: 2px solid var(--color-pri);
    font-family: 'Open Sans Condensed', sans-serif;
}

.home-container h2 p {  
    text-align: center;
    font-size: 18px;
    opacity: 1;
    font-weight: 100;
    margin-top: 1px;
    font-family: 'Open Sans', sans-serif;
}

.scroll-down {  
    transform: rotate(90deg);
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}

.scroll-down hr {   
    background-color: blue;
    width: 20px;
}

.scroll-down h5 {   
    font-size: 18px;
    text-transform: capitalize;
    font-weight: 100;
}

.scroll-down .scroll{   
    transform: rotate(-90deg);
    font-size: 100; 
}

